.aboutBody{
    width:100%;
    height: auto;
    display: grid;
    gap: 6em;
    justify-items: center;
    margin-top: 10%;
    
    
  }

  @media screen and (max-width: 730px),(orientation: portrait) {
    .aboutBody{
        display: block;
    }
}

.clownImg{
    margin-top: 1vh;
    width: 60%;
    height: auto;
    justify-self: center;
    
}

.experience {
    margin-top: 6em;
    margin-bottom: 5%;
}



.center{
    text-align: center;
  }





@media only screen and (max-width:900px){
    .clownImg{
        margin-top: 5%;
        margin-bottom: 5%;
        width: 100%;
        height: auto;
    }
  
}