  
.naturalisBody{
    height: auto;
    display: block;
    margin-top: 10%;
    
    width:100%;
    padding-bottom: 10%;
  }
  
  @media screen and (max-width: 730px),(orientation: portrait)  {
    .naturalisBody{display: block !important; width: 100% !important; text-align: center !important; height: 100% !important;}
    .tipulaGrid{display: block !important; text-align: center !important; width: 100% !important;}
    .sliceGrid{display:block !important; text-align: center !important; width: 100% !important;}
    .naturalisImgWrapper{display: block !important; margin-top: 0; padding-right: 10em !important; height: auto !important;}
    .three{margin-bottom: 4em; margin-top:4em !important;}
    iframe{
        margin-top: 4em;
        position: relative;
        left: -20%;     
    }

   
  }
.sliceGrid{
    display: grid;
    justify-items: center;
}

.naturalisImg{
  
    height: auto;
    padding-top: 7%;
    padding-bottom: 3%;
}

.one {
    
    width: 100%;
}

.two{
    width: 100%;
}

.three{
    width:80%;
}
.naturalisImgWrapper{
    padding-top: 10%;
    width: inherit;
    display:grid;
    justify-items: center;
    grid-template-rows: auto;
    height: 100%;
    margin:0;
}

iframe{
    width: 600px;
    height: 720px;
    
    
}

.tipulaGrid{
    display: grid;
    gap: 2%;
    grid-template-columns: 50% 50%;
    align-items: center;
    justify-items: center;
}

.tipulaGridRight {
    display:grid;
    gap: 3%;
    width: 90%;
    grid-template-rows: auto 5% auto;
  
}