.somzoaBody{
    width: 91vh;
    height: auto;
    display: grid;
    margin-top: 10%;
    grid-template-rows: 15vh 16vh 5vh 310vh 5vh ;
    
    grid-template-columns: 91vh;
  }

  @media screen and (max-width: 730px),(orientation: portrait)  {
      .somzoaBody{width: 100%; display: block}
      .buttons{height: auto;}
      .somzoaProjects{display: block !important; width: 100%; height: 0 !important}
      .szSectionTwo {display : block !important}
      .szSectionTwo, iframe{
        width: 90vw;
        position: relative;
        left: 0%;
    }
 
  }
  
  

.somzoaProjects{
    height: 100%;
    display: block;
    margin-bottom: 20em;
}
    

