.virusBody{
    height: auto;
    display: grid;
    margin-top: 10%;
    grid-template-rows: 25.5vh 14vh 4vh 40vh 5vh 75vh 5vh 80vh 5vh 10vh 25vh 10vh;
   
  }

  @media screen and (max-width: 730px),(orientation: portrait) {
      .virusBody{
          display: block;
          text-align: left;
      }
      .sectionTwo, .sectionThree , .sectionFour , .sectionFive{
          margin-top: 1.7em;
      }
      
  }
 

.topImage {
    width: 100%;
    height: auto;
}

.sectionTwo {
    display: grid;
    justify-items: center;
    
    background-color:#A6A39E;
    height: 100%;
}

.sectionThree {
    background-color:#f2efe9;
    height: 100%;
}

.sectionFour {
    background-color:#DEDAD4;
    height: 100%;
    
}

.sectionFive {
    background-color: white;
    height: 100%;
}

.virusAppImgSectionTop{
   
    margin-top: 4.5%;
    margin-right: 1.5%;
    width: 95%;
    height: auto;
}

.virusAppImgSectionTwo{
    width: 100%;
    height: auto;
}


.virusAppImgSectionThree{
    margin-top: 3%;
    width: 100%;
    height: auto;

} 
.virusAppImgSectionFour{
    width: 100%;
    height: auto;

} 