#myScrollbar {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

p, a {
  color:white;
  font-family: 'Roboto', sans-serif;
  font-size: 1.4vh;
  text-decoration: none;

}
h2{
  color:white;
  font-family: 'Roboto', sans-serif;
}

a:hover {
  color: red;
}
.links {
  font-size: 2rem;
}

.hi {
  font-size: 1.7vh;
}
.cV{
  margin-top: 1vh;
  color: #E57070;
  font-size: 1.4vh;
  text-decoration: underline;
}

.cV:hover{
  text-decoration: white;
}


@media screen and (max-width: 730px),(orientation: portrait)  {
  
 
  .cV{padding-top: 2em;}
}


#root {
  display: block;
  grid-template-columns:  100%;
  justify-content: center;
  background-color: black;
  position: relative;
  z-index: 0;
  height: 100%;
  
}



.appGrid {
  display: grid;
  grid-template-columns: 0vh 91vh 0vh;
  
  justify-content: center;
  
  background-color:black;
  position: relative;
  z-index: 0;
}

@media screen and (max-width: 730px),(orientation: portrait)  {
  .appGrid {grid-template-columns: 0vh 91vw 0vh;}
}








.quoteHeader{
  margin-top: 6%;
    font-size: 4.7vh;
}

@media screen and (max-width: 730px),(orientation: portrait)  {
  .quoteHeader{margin-bottom: 1em}
}



.infoText{
    font-size: 0.8rem;
    text-align: justify;
    color: whitesmoke;
    
}

@media screen and (max-width: 730px),(orientation: portrait)  {
  .infoText{margin-bottom: 3em; text-align: none !important;}
}









  button{
    
    font-size: 0.9vh;
    z-index: 1000;
    width: 11vh;
    height: 2.3vh;
    background-color: red;
    color:white;
    border-radius: 0;
    border-style:hidden;
    margin-right: 4%;
    margin-top: 2%;
  }

  button:hover{
    color:black;
    cursor: pointer;
  }

  .buttons {
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 100%;
    width: 23vh;
    gap: 2vh;
    
    

  }





























.footer {
  bottom: 0;
  position: relative;
  display: grid;
  grid-template-rows: 1fr 1fr;
  height: 10vh;
  width: 100%;
  border-top: 0.1vh solid #E57070;
  margin-bottom: 10vh;
}

.footerText{
  margin-top: -4.3vh;
}


@media screen and (max-width: 730px),(orientation: portrait)  {
  .footer{
    margin-bottom: 0;
    margin-top: 10vh;
  }


}